














































.t-alert {
  background-color: #000;
  border-radius: .03rem;
  color: #fff;
  position: fixed;
  top: 60%;
  margin: 0 auto;
  text-align: center;
  padding: .05rem;
  font-size: .14rem;
}

.t-mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
}

.t-conform {
  position: fixed;
  top: 35%;
  left: 10%;
  width: 80%;
  background-color: #fff;
  border-radius: .03rem;
  text-align: center;
}

.t-conform .msg {
  line-height: 1.5;
  padding: .2rem;
  border-bottom: .01rem solid #e5e5e5;
  font-size: .16rem;
}

.t-conform .opts {
  height: .4rem;
  line-height: .4rem;
}

.t-conform .opts li {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  position: relative;
  height: 100%;
  font-size: .14rem;
  border-right: .01rem solid #e5e5e5;
}

.t-conform .opts li:last-child {
  border-right: none;
}

.t-message {
  position: fixed;
  top: 35%;
  left: 10%;
  width: 80%;
  background-color: #fff;
  border-radius: .03rem;
  text-align: center;
}

.t-message .warp {
  position: relative;
  width: 100%;
}

.t-message .title {
  height: .4rem;
  background-color: #03c5a3;
  font-size: .16rem;
  color: #fff;
  line-height: .4rem;
  border-top-left-radius: .03rem;
  border-top-right-radius: .03rem;
}

.t-message .msg {
  min-height: 1rem;
  padding: .1rem;
  text-align: left;
}

.t-message .img.close-white {
  position: absolute;
  right: .1rem;
  top: .1rem;
}
.t-message .opt {
  text-align: left;
  padding-left: .1rem;
  color: #aaaaaa;
  font-size: .12rem;
}

.t-loading {
  position: fixed;
  left: 50%;
  margin-left: -.3rem;
  top: 50%;
  margin-top: -.3rem;
  width: .6rem;
  height: .6rem;
}

.t-loading .warp {
  background-color: #000;
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: .1rem;
}

.t-commit {
  position: fixed;
  top: 35%;
  left: 10%;
  width: 80%;
  background-color: #fff;
  border-radius: .03rem;
  text-align: center;
}

.t-commit .warp {
  position: relative;
  width: 100%;
}

.t-commit .title {
  height: .4rem;
  font-size: .16rem;
  color: #333;
  line-height: .4rem;
}

.t-commit .content {
  height: 1.3rem;
  margin: 0 .2rem;
}

.t-commit .edit {
  padding: .1rem;
  width: 100%;
  height: 1.1rem;
  background-color: #f9f9f9;
  border: .01rem solid #e6e6e6;
  border-radius: .03rem;
}

.t-commit .opts {
  height: .4rem;
  line-height: .4rem;
  border-top: .01rem solid #e5e5e5;
}

.t-commit .opts li {
  position: relative;
  height: 100%;
  font-size: .14rem;
  border-right: .01rem solid #e5e5e5;
}

.t-commit .opts li:last-child {
  border-right: none;
}

.t-commit .img.close {
  position: absolute;
  right: .15rem;
  top: .12rem;
  width: .12rem;
  height: .12rem;
  background-size: .12rem;
}

.t-loading .img.loading {
  -webkit-transform: rotate(0deg) translateZ(0);
  -webkit-transition-duration: 0ms;
  width: .35rem;
  height: .35rem;
  background-size: .35rem;
  left: .125rem;
  top: .125rem;
  position: absolute;
  background-image: url(./assets/img/alert/loading.png);
  -webkit-animation-name: loading;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
}



