*, *:before, *:after {
  /* 设置元素的盒模型为border-box */
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  /* chrome不再支持下面的属性。移动端Safari横屏时禁止字体放大 */
  -webkit-text-size-adjust: 100%;
  /* 去除点击元素后的高亮效果 */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

/* 去除元素周围的虚线 */
*:focus {
  outline: none;
}

html {
  font-size: 625%; /* 相当于100px，方便计算，浏览器默认字体为16px */
  color: #666;
  background-color: #f7f7f7;
}

html,
body {
  width: 100%;
  overflow-x: hidden;
}

@font-face {
  font-family: 'myOpenSans';
  src: url('/fonts/OpenSans-Regular.TTF') format('truetype');
}

@font-face {
  font-family: 'myQuestrial';
  src: url('/fonts/Questrial-Regular.OTF') format('opentype');
}

body {
  font-size: 14px;
  line-height: 1.5;
  -webkit-user-select: none;
  user-select: none;
  font-family: 'myQuestrial', Helvetica Neue, Helvetica, Arial, sans-serif;
  /*Helvetica Neue,Helvetica,Arial,sans-serif*/
}

@media all and (max-width: 320px) {
  body {
    font-size: 12px;
  }
}

