  word-break: break-word;
}

i, em,
strong {
  font-style: normal;
  font-weight: normal;
}

i {
  display: inline-block;
}

button,
input,
select,
textarea {
  font-size: 100%;
  font-family: inherit;
  padding: 0;
  -webkit-appearance: none;
}

input::-webkit-input-placeholder,
input::-moz-placeholder,
textarea::-webkit-input-placeholder,
textarea::-moz-placeholder {
