}

.right {
  float: right;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

.clear-fix {
  clear: both;
}

.show {
  display: block;
}

.hide {
  display: none;
}

span.img {
  display: block;
}

