input[placeholder], [placeholder], *[placeholder] {
  color: #333;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

/*control box*/
.select-box, .radio-box, .control-box {
  position: relative;
}

.select-box .self, .radio-box .self, .control-box .self {
  z-index: 1;
  position: absolute;
}

.select-box select, .radio-box input[type='radio'], .control-box .ctrl-box {
  z-index: 2;
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}

/*button*/
.button-style {
  border: none;
  border-radius: .05rem;
  text-align: center;
}

/*z-index*/
.z-index-max {
  z-index: 10000;
}

.z-index-1 {
  z-index: 9999;
}

.z-index-2 {
  z-index: 999;
}

/* === section === */
.section {
  border-top: .01rem solid #efefef;
  border-bottom: .01rem solid #efefef;
  margin-bottom: .1rem;
  background-color: #fff;
}

.section .matter {
  height: .45rem;
  margin-left: .1rem;
  line-height: .45rem;
  border-bottom: .01rem solid #efefef;
  position: relative;
  text-align: left;
  font-size: .16rem;
}

.section .matter:last-child {
  border-bottom: none;
}

.matter .title {
  width: 25%;
  float: left;
  height: 100%;
}

.matter .edit {
  float: left;
  width: 70%;
  height: 100%;
}

.matter .edit input[type='text'].full {
  width: 100%;
}

.matter .edit input[type='text'] {
  padding: 0;
}

.img.transfer {
  position: absolute;
  right: .1rem;
  top: .15rem;
  width: .08rem;
  height: .12rem;
  background-size: .08rem;
  background-image: url(../img/icon/arrow-right.png);
}

/*scroll*/
.over-scroll {
  overflow: scroll;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

/*transaction*/
.tran-all-3 {
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.tran-forword {
  -webkit-transition: right .3s cubic-bezier(.35, 0, .25, 1), left .3s cubic-bezier(.35, 0, .25, 1) .09s;
  transition: right .3s cubic-bezier(.35, 0, .25, 1), left .3s cubic-bezier(.35, 0, .25, 1) .09s;
}

.tran-scale {
  -webkit-transition-duration: .4s;
  transition-duration: .4s;
  -webkit-transform: scale(1) !important;
  transform: scale(1) !important;
  transition-property: transform, -webkit-transform !important;
}

.tran-scale1 {
  -webkit-transform: scale(1.1) !important;
  transform: scale(1.1) !important;
}

/*img*/
.img.close {
  width: .1rem;
  height: .1rem;
  background-size: .1rem;
  background-image: url(../img/icon/closeBtn03.png);
}

.img.close-white {
  width: .16rem;
  height: .16rem;
  background-size: .16rem;
  background-image: url(../img/icon/close.png);
}

/*none-bg*/

.none_bg {
  position: relative;
  width: 100%;
  height: 100%;
}

.none_bg .img {
  position: absolute;
  left: 50%;
  margin-left: -0.5rem;
  top: 40%;
  margin-top: -0.5rem;
  width: 1rem;
  height: 1rem;
  background-size: 1rem;
}

.none_bg.need-login .img {
  width: .8rem;
  height: .8rem;
  margin-top: -.3rem;
  background-size: .8rem;
  margin-left: -.4rem;
  margin-right: -.4rem;
}

.none_bg .remark {
  position: absolute;
  top: 40%;
  left: 15%;
  right: 15%;
  margin-top: .6rem;
  text-align: center;
  color: #999;
}

.none_bg .tip {
  position: absolute;
  text-align: center;
  top: 40%;
  margin-top: .9rem;
  width: 1rem;
  height: .35rem;
  left: 50%;
  margin-left: -.5rem;
  border-radius: .05rem;
  background-color: transparent;
  border: .01rem solid #00cda9;
  color: #00cda9;
}

.fade-transition {
  transition: opacity .3s ease;
}

.fade-enter, .fade-leave {
  opacity: 0;
}

.confirm-ok {
  height: .45rem;
  line-height: .45rem;
  width: 90%;
  background-color: #00CDA9;
  color: #fff;
  font-size: .16rem;
  text-align: center;
  border-top: .01rem solid #e5e5e5;
  border-bottom: .01rem solid #e5e5e5;
  position: absolute;
  left: 5%;
  border-radius: .05rem;
  margin-top: .25rem;
}

.confirm-nk {
  height: .45rem;
  line-height: .45rem;
  width: 90%;
  background-color: #00cda9;
  color: #49ddc3;
  font-size: .16rem;
  text-align: center;
  border-top: .01rem solid #e5e5e5;
  border-bottom: .01rem solid #e5e5e5;
  position: absolute;
  left: 5%;
  border-radius: .05rem;
  margin-top: .25rem;
}

/*week*/
.week {
  border-radius: .02rem;
  padding: .02rem .05rem;
  color: #fff;
  font-size: .1rem;
  vertical-align: middle;
  position: relative;
}

.week:before {
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -.04rem;
  left: -.04rem;
  border-top: .04rem solid transparent;
  border-bottom: .04rem solid transparent;
}

.weekdays.week:before {
  border-right: .04rem solid #7bc6ff;
}

.weekends.week:before {
  border-right: .04rem solid #ffa854;
}

.unnormal.week:before {
  border-right: .04rem solid #ff919b;
}

.week.weekdays {
  background-color: #7bc6ff;
}

.week.weekends {
  background-color: #ffa854;
}

.week.unnormal {
  background-color: #ff919b;
}

/*paypwd-box*/
.paypwd-box {
  position: fixed;
  top: 35%;
  left: 10%;
  width: 80%;
  background-color: #fff;
  border-radius: .03rem;
  text-align: center;
}

.paypwd-box .warp {
  margin: .1rem;
  position: relative;
}

.paypwd-box .title {
  font-size: .16rem;
  color: #333;
}

.paypwd-box .pay-detail {
  color: #999;
  margin-top: .15rem;
}

.paypwd-box .pwd-box {
  width: 100%;
  height: .4rem;
  line-height: .4rem;
  margin-top: .15rem;
  position: relative;
}

.pwd-box .real-box {
  position: absolute;
  left: -99.99rem;
  top: 0;
  width: 1px;
  height: 1px;
  z-index: -1;
}

.pwd-box .process {
  border: .01rem solid #e6e6e6;
  height: 100%;
  z-index: 2;
}

ul.process li {
  border-right: .01rem solid #e6e6e6;
  position: relative;
  height: 100%;
}

ul.process li:last-child {
  border-right: none;
}

ul.process .has:before {
  content: "";
  width: .1rem;
  height: .1rem;
  border-radius: .1rem;
  background-color: #000;
  position: absolute;
  left: 50%;
  margin-left: -.05rem;
  top: 50%;
  margin-top: -.05rem;
}

.forget-paypwd {
  text-align: right;
  font-size: .12rem;
  color: #999;
  display: block;
  margin-top: .1rem;
}

.img.close {
  right: 0;
  top: 0;
  position: absolute;
}



